import React from 'react';
import axios from 'axios';
import Movie from './Movie';
import "./App.css";
//constructure():자바스크립트에만 사용
//mounting():가장 먼저 실행됨(작동)
//render():두번째로 실행되고 componentDidMount를 실행함
//componentDidMount()
//componentWillUnount():Unmount를 실행함
//unmounting():component가 죽을때 실행됨
class App extends React.Component{
  state={
    isLoading: true,
    movies:[]
  };
  componentDidMount(){
    setTimeout(()=>{
      this.setState({isLoading:false});
    }, 6000)    
  }
  getMovies= async () => {
    const {
      data: {
        data: { movies }
      }
    } = await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");
    this.setState({movies, isLoading: false})// console.log(movies.data.data.movies)
  };
  componentDidMount(){
    this.getMovies();
  }
  render(){
    const {isLoading, movies}=this.state;
    return (
      <section className="container">      
        {isLoading ? (
        <div className="loader">
          <span className="loader__text">Loading...</span>
          </div> 
        ) : ( 
          <div className="movies">
            { movies.map(movie =>(
              // console.log(movie);
              <Movie
              key={movie.id} 
              id={movie.id}
              year={movie.year} 
              title={movie.title} 
              summary={movie.summary} 
              poster={movie.medium_cover_image}
              genres={movie.genres}
              />
            ))}
          </div>
         )}
      </section>
      );
  }
}

export default App;